    },
    copyToClipboard() {
      // ÂàõÂª∫‰∏Ä‰∏™‰∏¥Êó∂ÁöÑ textarea ÂÖÉÁ¥†Êù•ËøõË°åÂ§çÂà∂
      const textArea = document.createElement('textarea');
      textArea.value = this.text;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);

      // Â§çÂà∂ÊàêÂäüÂêéÔºåÊîπÂèòÊåâÈíÆËÉåÊôØÈ¢úËâ≤
      this.isCopied = true;

      // 2 ÁßíÂêéÊÅ¢Â§çÊåâÈíÆÂéüÂßãÈ¢úËâ≤
      setTimeout(() => {
        this.isCopied = false;
      }, 500);
    }
  }
}
</script>


